export default {
    props: {
        disable: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            tapping: false
        }
    },
    computed: {
        tappingClass() {
            return this.disable ? "disable" : (this.tapping ? 'tapping' : '')
        }
    },
    methods: {
        clickBuntton() {
            !this.disable&&this.$emit('click', this.content)
        },
        clickStart() {
            this.tapping = true;
        },
        cancelTap() {
            this.tapping = false;
        }
    },
}
